import { useEffect, useState } from "react"
import { useAppSelector } from "../../../hooks/useRedux"
import { doc, getDoc } from "firebase/firestore"
import { auth, db } from "../../../configs/firebase.config"
import IMAGES from "../../../assets"

export default function Detail() {

    const user = auth.currentUser

    const [account, setAccount] = useState<any>(null)
    const uid = useAppSelector(state => state.auth.uid)

    const getAccount = async () => {
        const docRef = doc(db, "users", uid);
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
            setAccount(docSnap.data());
        }
    }

    useEffect(() => {
        getAccount()
    }, [])

    console.log(account);


    return (
        <div className="p-4 flex flex-col items-center gap-4" style={{
          paddingBottom: 128,
          overflow: "auto"
        }}>
            <div className="flex flex-col items-center">
                <div className="w-[6rem] h-[6rem] rounded-full overflow-hidden">
                    <img className="w-full h-full object-cover" src={
                        account?.photoURL ? account?.photoURL : IMAGES.Default
                    } alt="" />
                </div>
                <h1>{account?.fullName ? account?.fullName : '--------------'}</h1>
                <span>{user?.phoneNumber ? user?.phoneNumber : '--------------'}</span>
            </div>
            <div className="w-full">
                <h1 className="font-bold ml-8">Thông tin</h1>
                <div className="w-full flex flex-col gap-2 mt-6">
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Họ tên :</p>
                        <p className="flex-1">{account?.fullName ? account?.fullName : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Địa chỉ :</p>
                        <p className="flex-1">{account?.address ? account?.address : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Số CMND/CCCD :</p>
                        <p className="flex-1">{account?.cmnd ? account?.cmnd : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium"> Giới tính :</p>
                        <p className="flex-1">{account?.genre ? account?.genre : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Ngày sinh :</p>
                        <p className="flex-1">{account?.dateOfBirth ? account?.dateOfBirth : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Nghề nghiệp :</p>
                        <p className="flex-1">{account?.job ? account?.job : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Thu nhập :</p>
                        <p className="flex-1">{account?.income ? (
                            account?.income === '1' ? 'Dưới 5 triệu' : account?.income === '2' ? 'Từ 5 - 10 triệu' : account?.income === '3' ? 'Từ 10 - 20 triệu' : account?.income === '4' ? 'Trên 20 triệu' : '--------------'
                        ) : '----------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">
                            Mục đích khoản vay :
                        </p>
                        <p className="flex-1">{account?.purpose ? account?.purpose : '--------------'}T</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">SĐT người thân :</p>
                        <p className="flex-1">{account?.phoneNumber ? account?.phoneNumber : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Mối quan hệ với người thân :</p>
                        <p className="flex-1">{account?.relationship ? account?.relationship : '--------------'}</p>
                    </div>
                </div>
            </div>
            <div className="w-full mt-8">
                <h1 className="font-bold ml-8">Tài khoản ngân hàng</h1>
                <div className="w-full flex flex-col gap-2 mt-6">
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Tên ngân hàng : :</p>
                        <p className="flex-1">{account?.nameBank ? account?.nameBank : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Số thẻ ngân hàng:</p>
                        <p className="flex-1">{account?.numberBank ? account?.numberBank : '--------------'}</p>
                    </div>
                    <div className="w-full flex items-center justify-between">
                        <p className="flex-1 text-sm font-medium">Tên thụ hưởng :</p>
                        <p className="flex-1">{account?.nameAccount ? account?.nameAccount : '--------------'}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}
